import Vue from "vue";
import Router from "vue-router";
import About from "@/components/About";
import Hbd from "@/components/Hbd";

Vue.use(Router);

export default new Router({
  // mode: 'history',
  routes: [
    {
      path: "/",
      name: "About",
      component: About
    },
    {
      path: "/hbd",
      name: "Hbd",
      component: Hbd
    }
  ]
});



