<template>
  <nav class="navbar is-fixed-top is-transparent">
    <div class="container is-fluid">
      <div class="navbar-brand">
        <a class="navbar-item">
          <figure class="image is-24x24 has-image-centered">
            <img class="is-rounded avatar" src="../assets/noface.jpg" alt="logo">
          </figure>
        </a>
      </div>
      <div class="navbar-start">
        <router-link
          v-for="routes in links"
          v-bind:key="routes.id"
          :to="`${routes.page}`"
          class="navbar-item"
        >
          <span class="borderCenter about">{{routes.text}}</span>
        </router-link>
      </div>
    </div>
  </nav>
</template>

<style scoped>
@import url("https://fonts.googleapis.com/css?family=PT+Mono");
* {
  font-family: "PT Mono", sans-serif;
  color: #dcdcdc;
}

.navbar {
  background-color: transparent;
}

.borderCenter {
  position: relative;
}

.about {
  font-size: 1.2rem;
  color: #78707e;
}

.borderCenter::after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: -3px;
  width: 0%;
  height: 1px;
  opacity: 0;
  background-color: #dcdcdc;
  transform: translate(-50%, 0);
  transition: all 0.3s ease-in-out;
}

.borderCenter:hover::after {
  width: 100%;
  opacity: 1;
}
</style>

<script>
export default {
  name: "Navbar",
  data() {
    return {
      links: [
        { id: 0, text: "About/>", page: "/" }
      ]
    };
  }
};
</script>




