<template>
  <section class="section hero is-fullheight">
    <div class="hero-body">
      <div class="container">
        <div class="about">About/></div>
        <div class="code-container">
          <div class="code-line" data-line-nr="01">
            <div class="function">function</div>
            <span class="prop">about</span>
            {
          </div>
          <div class="code-line" data-line-nr="02">
            <div class="dot">····</div><span class="scope">this</span>.<span class="prop2">name</span>
            =
            <span class="string">{{name}}</span>
          </div>
          <div class="code-line" data-line-nr="03">
            <div class="dot">····</div><span class="scope">this</span>.<span class="prop2">dateofBirth</span>
            =
            <span class="new">new</span>
            <span class="date">Date</span>(<span class="string">{{dateofBirth}}</span>)
          </div>
          <div class="code-line" data-line-nr="04">
            <div class="dot">····</div><span class="scope">this</span>.<span class="prop2">email</span>
            =
            <span class="string">{{email}}</span>
          </div>
          <div class="code-line" data-line-nr="05">}</div>
          <div class="code-line" data-line-nr="06">
            <div class="function">function</div>
            <span class="prop">workExperience</span>()
            {
          </div>
          <div class="code-line" data-line-nr="07">
            <div class="dot">····</div><span class="function">return</span>
            [
          </div>
          <div class="code-line" data-line-nr="08">
            <div class="dot">········</div>{
            <span class="string">{{workExperience}}</span>
            }
          </div>
          <div class="code-line" data-line-nr="09">
            <div class="dot">····</div>]
          </div>
          <div class="code-line" data-line-nr="10">}</div>
          <div class="code-line" data-line-nr="11">
            <div class="function">function</div>
            <span class="prop">education</span>()
            {
          </div>
          <div class="code-line" data-line-nr="12">
            <div class="dot">····</div><span class="function">return</span>
            [
          </div>
          <div class="code-line" data-line-nr="13">
            <div class="dot">········</div>{
            <span class="string">{{education_university}}</span>
            },
          </div>
          <div class="code-line" data-line-nr="14">
            <div class="dot">········</div>{
            <span class="string">{{education_school}}</span>
            }
          </div>
          <div class="code-line" data-line-nr="15">
            <div class="dot">····</div>]
          </div>
          <div class="code-line" data-line-nr="16">}</div>
          <div class="code-line" data-line-nr="17">
            <div class="function">function</div>
            <span class="prop">skills</span>()
            {
          </div>
          <div class="code-line" data-line-nr="18">
            <div class="dot">····</div><span class="function">return</span>
            [
            <span class="string">{{skills}}</span>
            <span class="cursor"></span> ]
          </div>
          <div class="code-line" data-line-nr="19">}</div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: "About",
  data() {
    return {
      name: "'Teerawut Kitbunjerdjarud'",
      dateofBirth: "'November 8, 1996'",
      email: "'t.kitbunjerdjarud@gmail.com'",
      workExperience:
        "'Jun 2018 - Nov 2018' : 'Intern (Cooperative Education) at AIT'",
      education_school:
        "'2003 - 2015' : 'Assumption Samutprakarn School | English and Mathematics'",
      education_university:
        "'2015 - 2019' : 'King Mongkut’s Institute of Technology Ladkrabang | Network and System'",
      skills:
        "'HTML/CSS/JS', 'Vue.js', 'jQuery', 'Bootstrap/Bulma', 'npm', 'Python', 'Firebase', 'MySQL', 'MariaDB', 'API', 'Photoshop'"
    };
  }
};
</script>

<style scoped>
@import url("https://fonts.googleapis.com/css?family=Roboto+Mono");

* {
  background-color: #1a1527;
  color: #f9f9f9;
  font-family: Roboto Mono, Courier New, Courier, monospace !important;
  font-weight: 400;
}

::selection {
  color: #fff;
  background-color: #5918df;
}

.code-container {
  font-size: 16px;
  line-height: 1.6;
}

.code-line {
  z-index: 1;
  position: relative;
}

.code-line::after {
  content: attr(data-line-nr);
  position: absolute;
  left: -60px;
  color: #3b3553;
  font-family: Roboto Mono, Courier New, Courier, monospace !important;
  font-size: 16px;
  line-height: 1.6;
  z-index: 3;
  top: 0;
}

.about {
  margin-bottom: 30px;
  font-size: 18px;
  font-weight: 300;
  color: #988e9f;
}

.new {
  color: #e39d24;
}

.scope {
  color: #e3244c;
  display: inline;
}

.function {
  color: #5918df;
  display: inline;
}
.dot {
  color: #3b3553;
  display: inline;
}
.string {
  color: #24e33a;
}
.prop {
  color: #eeff31;
}

.prop2 {
  color: #24e394;
}

.date {
  color: #ff1493;
}

.cursor:before {
  opacity: 0;
  content: "|";
  -webkit-animation: blink 0.8s infinite;
  animation: blink 0.8s infinite;
}

@-webkit-keyframes blink {
  0% {
    opacity: 0;
  }

  50% {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

@keyframes blink {
  0% {
    opacity: 0;
  }

  50% {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}
</style>


